import { cn } from "../../utils/custom_functions";
import { IoIosArrowBack, IoIosArrowForward } from "react-icons/io";

export function Paginator({ currentPage, maxPages, onPageChange, ...props }) {
  const minPages = 1;
  const previousPageChange = () => {
    if (currentPage > minPages) {
      return onPageChange(currentPage - 1);
    }
  };
  const nextPageChange = () => {
    if (currentPage < maxPages) {
      return onPageChange(currentPage + 1);
    }
  };
  return (
    <div {...props} className="max-w-fit">
      <div className="mb-[10px]">
        <p className="text-sm text-gray-800">
          Showing page <span className="font-bold">{currentPage}</span> of{" "}
          <span className="font-bold">{maxPages}</span> Pages
        </p>
      </div>
      <div className="flex flex-row items-center">
        <button
          onClick={previousPageChange}
          disabled={currentPage <= minPages}
          className={cn(
            "border px-3 w-full py-2 rounded-s-md text-sm block bg-yellow-400 border-yellow-400 hover:bg-yellow-500 disabled:opacity-50 disabled:hover:bg-yellow-400 disabled:hover:border-yellow-400 hover:border-yellow-500 text-slate-800 hover:text-slate-950 disabled:hover:text-slate-800",
            {}
          )}
        >
          <div className="flex flex-row justify-center items-center gap-1">
            <div>
              <IoIosArrowBack />
            </div>
            <span className="select-none">Previous</span>
          </div>
        </button>
        <button
          onClick={nextPageChange}
          disabled={currentPage >= maxPages}
          className={cn(
            "border px-3 w-full py-2 rounded-e-md text-sm block bg-yellow-400 border-yellow-400 disabled:opacity-50 disabled:hover:bg-yellow-400 disabled:hover:border-yellow-400 hover:bg-yellow-500 hover:border-yellow-500 text-slate-800 hover:text-slate-950 disabled:hover:text-slate-800",
            {}
          )}
        >
          <div className="flex flex-row justify-center items-center gap-1">
            <span className="select-none">Next</span>
            <div>
              <IoIosArrowForward />
            </div>
          </div>
        </button>
      </div>
    </div>
  );
}
