import { cn } from "../../../utils/custom_functions";
import { Pickupdropoff } from "./pickup_dropoff_content";
import { BsInfoCircle as InfoIcon } from "react-icons/bs";
import { Tooltip } from "flowbite-react";
import moment from "moment";

import {
  BASE_AMOUNT,
  LOYAL_CUSTOMER_DISCOUNT,
  MID_MONTH_DISCOUNT,
} from "../../../utils/information";

export function BookingDetailsContent({ booking }) {
  return (
    <table
      className={cn(
        "table-auto border border-yellow-100 mt-4 w-full border-separate border-spacing-0 rounded-md shadow-sm",
        {
          "border-red-100": booking?.booking_canceled,
          "border-gray-100": booking?.booking_completed,
        }
      )}
    >
      <thead
        className={cn("bg-yellow-200", {
          "bg-red-200": booking?.booking_canceled,
          "bg-gray-200": booking?.booking_completed,
        })}
      >
        <tr>
          <th className="rounded-ss-md text-start px-4 py-3">Description</th>
          <th className="rounded-tr-md text-start px-4 py-3">Details</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td className="px-4 py-3 text-sm">Booking date</td>
          <td className="px-4 py-3 text-sm">
            {/* {booking?.booking_date} */}
            {moment(new Date(booking?.booking_date)).format("YYYY-MMM-DD")}
          </td>
        </tr>
        <tr>
          <td className="px-4 py-3 text-sm">Booking time</td>
          <td className="px-4 py-3 text-sm">
            {/* {booking?.booking_time} */}
            {moment(
              new Date(`${booking?.booking_date} ${booking?.booking_time}`)
            ).format("HH:mm")}
          </td>
        </tr>
        <tr>
          <td className="px-4 py-3 text-sm">Vehicle type</td>
          <td className="px-4 py-3 text-sm">{booking?.vehicle_type} Ton</td>
        </tr>
        <tr>
          <td className="px-4 py-3 text-sm">Helpers</td>
          <td className="px-4 py-3 text-sm">{booking?.helpers}</td>
        </tr>
        <tr>
          <td className="px-4 py-3 text-sm">Floors</td>
          <td className="px-4 py-3 text-sm">{booking?.floors}</td>
        </tr>
        <tr>
          <td className="px-4 py-3 text-sm">Payment option</td>
          <td className="px-4 py-3 text-sm">{booking?.payment_option}</td>
        </tr>

        {String(booking?.customer_driver_note).length > 0 && (
          <tr>
            <td colSpan={2} className="px-4 py-3 text-sm">
              <div>
                <div>
                  <p>Drivers note:</p>
                  <p className="text-gray-400">
                    {booking?.customer_driver_note}
                  </p>
                </div>
              </div>
            </td>
          </tr>
        )}

        <tr>
          <td colSpan={2} className="px-4 py-3 text-sm">
            <div>
              <div className="py-2 w-full flex justify-start">
                <span className="text-sm font-bold ">
                  Pick up and drop off locations{" "}
                </span>
              </div>
              <div className="mt-1">
                {booking?.pickup_dropoff_routes.map(
                  (pickup_dropoff_routes, i) => {
                    return (
                      <Pickupdropoff
                        key={i}
                        pickup_dropoff_route={pickup_dropoff_routes}
                      />
                    );
                  }
                )}
              </div>
              <div className="w-full flex justify-end pt-2">
                <p className="font-normal text-xs text-gray-400">
                  Total distance{" "}
                  <span className="font-bold">{booking?.distance}</span> kms
                </p>
              </div>
            </div>
          </td>
        </tr>
        <tr>
          <td
            className={cn(
              "px-4 py-3 text-sm border-t border-t-yellow-100 flex flex-row justify-start items-center gap-1",
              {
                "border-t-red-100": booking?.booking_canceled,
                "border-t-gray-100": booking?.booking_completed,
              }
            )}
          >
            Base price{" "}
            <Tooltip content={BASE_AMOUNT} placement="top" trigger="click">
              <InfoIcon className="text-yellow-500 hover:opacity-75" />
            </Tooltip>
          </td>
          <td className="px-4 py-3 text-sm border-t border-t-yellow-100 ">
            R {booking?.base_amount}{" "}
          </td>
        </tr>
        <tr className="border-b">
          <td className="px-4 py-3 text-sm flex flex-row justify-start items-center gap-1">
            Mid month discount{" "}
            <Tooltip
              content={MID_MONTH_DISCOUNT}
              placement="top"
              trigger="click"
            >
              <InfoIcon className="text-yellow-500 hover:opacity-75" />
            </Tooltip>
          </td>
          <td className="px-4 py-3 text-sm text-red-500">
            R -{booking?.mid_month_discount}{" "}
          </td>
        </tr>
        <tr>
          <td className="px-4 py-3 text-sm flex flex-row justify-start items-center gap-1">
            Loyal discount
            <Tooltip
              className="bg-slate-800"
              content={LOYAL_CUSTOMER_DISCOUNT}
              placement="top"
              trigger="click"
              arrow={true}
            >
              <InfoIcon className="text-yellow-500 hover:opacity-75" />
            </Tooltip>
          </td>
          <td className="px-4 py-3 text-sm text-red-500 ">
            R -{booking?.loyal_customer_discount}{" "}
          </td>
        </tr>
        <tr>
          <td
            className={cn(
              "px-4 py-3 text-lg  bg-yellow-200 font-extrabold flex flex-row justify-start items-center gap-2",
              {
                "bg-red-200": booking?.booking_canceled,
                "bg-gray-200": booking?.booking_completed,
              }
            )}
          >
            Balance due
          </td>
          <td
            className={cn("px-4 py-3 text-lg font-extrabold bg-yellow-200", {
              "bg-red-200": booking?.booking_canceled,
              "bg-gray-200": booking?.booking_completed,
            })}
          >
            R {booking?.amount_due_customer}{" "}
          </td>
        </tr>
      </tbody>
    </table>
  );
}
